form {
  margin: auto;
  background-color: rgb(111, 178, 198, 0.8);
}

.button {
  position: -webkit-sticky;
  position: sticky;
}

.form-body {
  padding: 10px;
  display: flex;
  flex-direction: center;
}

input {
  width: 100%;
  height: 30px;
  border-radius: 5px;
  background-color: rgba(176, 225, 251, 0.8);
}

input.input::-webkit-input-placeholder {
  color: #202834;
  font-size: larger;
}

input.input:-ms-input-placeholder {
  color: #202834;
  font-size: larger;
}

input.input::placeholder {
  color: #202834;
  font-size: larger;
}

.story {
  color: #a7f5ff;
}

nav {
  background-color: #202834;
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 0;
  width: 100%;
}

.footer {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #202834;
  height: 60px;
}

